@import url('https://fonts.googleapis.com/css2?family=Inter:wght@400;600;700&display=swap');

* {
  margin: 0;
  box-sizing: border-box;
}

body {
  margin: 0;
  background-color: #23252C;
  color: white;
  height: 100vh;
  font-family: 'Inter', -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.container {
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
}

.card {
  width: 317px;
  height: fit-content;
  margin-top: auto;
}

.card-header {
  /* overflow: hidden; */
  border-radius: 50px 50px 0px 0px;
  height: 317px;
  background: url("./images/pp.jpg") no-repeat;
  background-size: contain;

}

.card-header img {
  width: 317px;
}

.card-main {
  text-align: center;
  background-color: #1A1B21;
  padding: 10px 35px;
}

.card-main-header {
  padding-block: 10px;
}

.card-main h5 {
  font-weight: 400;
  color: #F3BF99;
  margin-block: 5px;
}

.card-main a {
  font-size: 10px;
  color: #F5F5F5;
  text-decoration: none;
}

.card-main a:hover {
  text-decoration: underline;
}

.card-main-buttons {
  padding: 10px 0;
  display: flex;
  justify-content: space-between;

}

.card-main-buttons button {
  display: flex;
  align-items: center;
  justify-content: center;
  box-shadow: 0;
  border: 0;
  border-radius: 5px;
  width: 115px;
  height: 34px;
  font-size: 14px;
  color: #374151;
  background-color: white;
}

.card-main-buttons button .icon {
  padding-right: 5px;
  padding-top: 4px;
}

button.linkedin-btn {
  color: white;
  background-color: #5093E2;
}

.card-main-content {
  text-align: left;
}

.card-main-content h4 {
  margin-block: 10px;
}

.card-main-content p {
  font-size: 10px;
  line-height: 1.3rem;
  color: #DCDCDC;
}

.card-footer {
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #161619;
  height: 64px;
  border-radius: 0 0 50px 50px;
}